// 计算zks分数
export function calcZksScore(data, amout) {
  let score = 0;
  const scoreArr = [0.2,0.4,0.9,1.2,1.3,1.4,1.5,1.6,1.7,1.8,2,2.5,3.5,4,4.5,5,7,9.5,20,30];
  // lite
  if (data['liteBridge'] != "0.0000/0.0") {
    score++;
  }
  // liteTx
  if (data['liteTx'] >= 10) {
    score++;
  } else if (data['liteTx'] >= 25) {
    score++;
  }
  // era + bridge
  let bridge = (data['bridge']).split("/");
  let usd = bridge[0] * 2000 + bridge[1];
  if (usd > 0) {
    score++;
  } else if (usd >= 10000) {
    score++;
  } else if (usd >= 25000) {
    score++;
  } else if (usd >= 50000) {
    score++;
  } else if (usd >= 100000) {
    score++;
  }
  // eraTx
  if (data["eraTx"] >= 10) {
    score++;
  } else if (data["eraTx"] > 25) {
    score++;
  } else if (data["eraTx"] > 100) {
    score++;
  }
  // contract
  if (data["contract"] >= 10) {
    score++;
  } else if (data["contract"] > 25) {
    score++;
  } else if (data["contract"] > 50) {
    score++;
  }
  // month
  if (data["month"] >= 3) {
    score++;
  } else if (data["month"] > 6) {
    score++;
  } else if (data["month"] > 9) {
    score++;
  }
  // week
  if (data["week"] >= 10) {
    score++;
  } else if (data["week"] > 25) {
    score++;
  } else if (data["week"] > 50) {
    score++;
  }
  // 算排名
  let rank = 0;
  for (let i = 0; i <= 20 - score; i++) {
    rank += scoreArr[i] * 10000;
  }
  rank = rank + 4256 - data["day"]
  // 算百分比
  let percent = (rank * 100 / amout).toFixed(2);
  // 算品质
  let quality = "反撸";
  if (rank <= 10 * 10000) {
    quality = "精品";
  } else if (rank <= 30 * 10000) {
    quality = "路人";
  } else if (rank <= 50 * 10000) {
    quality = "保底";
  }
  return [score, rank, percent, quality];
}

