import axios from 'axios';
import {analyzeZkTx} from "./protocols/zksync";
import {formatDate, getAge, toMonday} from "./format";

// zks
// 定义 RPC_MAP 变量
const RPC_MAP = {
  ethereum: "https://eth.llamarpc.com",
  bsc: "https://bscrpc.com",
  avax: "https://avalanche.public-rpc.com",
  polygon: "https://polygon-bor.publicnode.com",
  arbitrum: "https://rpc.ankr.com/arbitrum",
  optimism: "https://optimism-mainnet.public.blastapi.io",
  ftm: "https://1rpc.io/ftm",
  metis: "https://andromeda.metis.io/?owner=1088",
  zksyncera: "https://mainnet.era.zksync.io"
}
// layerzero: Ethereum, BNB, Avalanche, Polygon, Arbitrum, Optimism, Fantom, Metis
// layerzero: 0x1, 0x38, 0xa86a, 0x89, 0xa4b1, 0xa, 0xfa, 0x440
export async function getEthBalance(network, walletAddress) {
  try {
    let rpcLink = RPC_MAP[network];
    if (!rpcLink) {
      return "Error: Invalid Network Name";
    }
    const response = await axios.post(rpcLink, {
      jsonrpc: "2.0",
      method: "eth_getBalance",
      params: [walletAddress, "latest"],
      id: 1
    });
    let balance = response.data.result;
    return (parseInt(balance, 16) / 10 ** 18).toFixed(4);
  } catch (error) {
    console.error(error);
    return "Error";
  }
}

// 获取 zkSync Era 的tx
// 废弃接口: "/transactions?direction=older&offset=0&limit=100&accountAddress="
export async function getZkSyncTx(address) {
  const ethRes = await axios.post('https://mainnet.era.zksync.io/', {
    id: 42,
    jsonrpc: '2.0',
    method: 'zks_getTokenPrice',
    params: ['0x0000000000000000000000000000000000000000'],
  });
  // get tx
  let txData = [];
  let retData;
  let params = "transactions?page=1&limit=10&address=" + address;
  do {
    await axios.get("https://block-explorer-api.mainnet.zksync.io/" + params).then(res => {
      txData = txData.concat(res.data.items);
      params = res.data.links.next;
    });
  } while (params != "");
  txData.forEach((transaction) => {
    transaction.ethPrice = ethRes.data.result;
  });
  // 整理tx数据，得出 更新/合约数/活跃月/周/日/总/gas/bridge
  let [recent, srecent, contracts, months, weeks, days, age, fee, bridge, erc20u] = ['', '', [], 0, 0, 0, 0, 0, 0, 0];
  let [preYear, preMonth, preDay] = [0, 0, 0];
  // let [erc20e, erc20u] = [0, 0];
  for (let i = 0 ; i < txData.length; i++) {
    if (i == 0) {
      recent = formatDate(txData[i]["receivedAt"], "M/D");
      srecent = formatDate(txData[i]["receivedAt"], "AGO");
    } else if (i == txData.length - 1) {
      age = getAge(txData[i]["receivedAt"]);
    }
    fee += Number(txData[i].fee)/(10**18);
    // 跨链桥次数及跨链金额
    if (txData[i]["isL1Originated"]) {
      bridge++;
      erc20u += txData[i].ethPrice*Number(txData[i].value)/(10**18);
    }
    // 合约数量
    if (contracts.indexOf(txData[i].to) < 0) {
      contracts.push(txData[i].to);
    }
    // 判断月周日活跃
    const nowTxDatetime = txData[i].receivedAt;
    const date = new Date(nowTxDatetime);
    const year = date.getFullYear();
    const month = date.getMonth() + 1; // 获取月份，需要加1
    const day = date.getDate();
    if ((year == preYear) && (month != preMonth) || (year != preYear)) {
      months++;
    }
    let md1 = toMonday(date);
    let md2 = toMonday(new Date(preYear+ "-" + preMonth + "-" + preDay));
    if (md1 != md2) {
      weeks++;
    }
    if (!((year == preYear) && (month == preMonth) && (day == preDay))) {
      days++;
    }
    [preYear, preMonth, preDay] = [year, month, day];
  }
  // 整理tx数据，获取合约详情
  let retAnalyze = analyzeZkTx(txData, address);
  return [recent, srecent, contracts.length, months, weeks, days, age, fee, txData.length, bridge, erc20u.toFixed(1), retAnalyze];
}

// 获取 zkSync Lite 的tx
export async function getZkLiteTx(address) {
  let txData = [];
  let i = 0; // 请求起始数
  let total = 100;
  let [erc20e, erc20u] = [0, 0];
  while (total == 100) {
    let url = "https://api.zksync.io/api/v0.1/account/" + address + "/history/" + (100 * i) + "/100";
    await axios.get(url).then(async res => {
      total = res.data.length;
      txData = txData.concat(res.data);
      i++;
    });
  }
  const ethRes = await axios.post('https://mainnet.era.zksync.io/', {
    id: 42,
    jsonrpc: '2.0',
    method: 'zks_getTokenPrice',
    params: ['0x0000000000000000000000000000000000000000'],
  });
  let ethPrice = ethRes.data.result;
  // 整理tx数据，得出 跨链桥
  for (let i = 0 ; i < txData.length; i++) {
    // 跨链桥次数及跨链金额
    if (txData[i]["tx"]["type"] == "Deposit") {
      let token = txData[i]["tx"]["priority_op"]["token"];
      if (token == "ETH") {
        erc20e += Number(txData[i]["tx"]["priority_op"]["amount"]) / (10**18);
      } else if (token == "USDT" || token == "USDC") {
        erc20u += Number(txData[i]["tx"]["priority_op"]["amount"]) / (10**18);
      }
    }
  }
  return [txData.length, (erc20e*ethPrice+erc20u).toFixed(1)];
}

// 获取 Starknet 的 余额、域名
export async function getSnEthBalance(addr) {
  try {
    const url = "https://starkscan.stellate.sh";
    const headers = {
      'authority': "starkscan.stellate.sh",
      'accept': "application/json",
      "accept-language": "zh-CN,zh;q=0.9,en;q=0.8,en-GB;q=0.7,en-US;q=0.6",
      "content-type": "application/json",
    };
    const Json_data = {
      query:
        "query ContractPageQuery(\n  $input: ContractInput!\n) {\n  contract(input: $input) {\n    contract_address\n    is_starknet_class_code_verified\n    implementation_type\n    ...ContractPageOverviewTabFragment_contract\n    ...ContractPageClassCodeHistoryTabFragment_contract\n    ...ContractFunctionReadWriteTabFragment_contract\n    id\n  }\n}\n\nfragment ContractFunctionReadCallsFragment_starknetClass on StarknetClass {\n  is_code_verified\n  abi_final\n}\n\nfragment ContractFunctionReadWriteTabFragment_contract on Contract {\n  contract_address\n  starknet_class {\n    ...ContractFunctionReadCallsFragment_starknetClass\n    ...ContractFunctionWriteCallsFragment_starknetClass\n    id\n  }\n}\n\nfragment ContractFunctionWriteCallsFragment_starknetClass on StarknetClass {\n  is_code_verified\n  abi_final\n}\n\nfragment ContractPageClassCodeHistoryTabFragment_contract on Contract {\n  contract_address\n  starknet_class {\n    is_code_verified\n    id\n  }\n  ...ContractPageCodeSubTabFragment_contract\n}\n\nfragment ContractPageCodeSubTabFragment_contract on Contract {\n  starknet_class {\n    class_hash\n    ...StarknetClassCodeTabFragment_starknetClass\n    id\n  }\n}\n\nfragment ContractPageOverviewTabClassHashPlacedAtItemFragment_contract on Contract {\n  deployed_at_transaction_hash\n  class_hash_placed_at_transaction_hash\n  class_hash_placed_at_timestamp\n}\n\nfragment ContractPageOverviewTabEthBalanceItemFragment_contract on Contract {\n  eth_balance {\n    balance_display\n    id\n  }\n}\n\nfragment ContractPageOverviewTabFragment_contract on Contract {\n  contract_address\n  class_hash\n  name_tag\n  is_social_verified\n  deployed_by_contract_address\n  deployed_by_contract_identifier\n  deployed_at_transaction_hash\n  deployed_at_timestamp\n  ...ContractPageOverviewTabEthBalanceItemFragment_contract\n  ...ContractPageOverviewTabTypeItemFragment_contract\n  ...ContractPageOverviewTabStarknetIDItemFragment_contract\n  starknet_class {\n    ...StarknetClassVersionItemFragment_starknetClass\n    id\n  }\n  ...ContractPageOverviewTabClassHashPlacedAtItemFragment_contract\n}\n\nfragment ContractPageOverviewTabStarknetIDItemFragment_contract on Contract {\n  starknet_id {\n    domain\n  }\n}\n\nfragment ContractPageOverviewTabTypeItemFragment_contract on Contract {\n  implementation_type\n  starknet_class {\n    type\n    id\n  }\n}\n\nfragment StarknetClassCodeTabAbiAndByteCodeItemFragment_starknetClass on StarknetClass {\n  is_code_verified\n  abi_final\n  bytecode\n  sierra_program\n}\n\nfragment StarknetClassCodeTabFragment_starknetClass on StarknetClass {\n  ...StarknetClassCodeTabVerifiedItemFragment_starknetClass\n  ...StarknetClassCodeTabSourceCodeItemFragment_starknetClass\n  ...StarknetClassCodeTabAbiAndByteCodeItemFragment_starknetClass\n}\n\nfragment StarknetClassCodeTabSourceCodeItemFragment_starknetClass on StarknetClass {\n  class_hash\n  verified {\n    source_code\n  }\n}\n\nfragment StarknetClassCodeTabVerifiedItemFragment_starknetClass on StarknetClass {\n  is_code_verified\n  verified {\n    name\n    source_code\n    verified_at_timestamp\n  }\n}\n\nfragment StarknetClassVersionItemFragment_starknetClass on StarknetClass {\n  is_cairo_one\n}\n",
      variables: {
        input: {
          contract_address: addr,
        },
      },
    };
    const response = await axios.post(url, Json_data, {headers: headers});
    const eth_balance = response.data.data.contract.eth_balance.balance_display;
    const stark_id = response.data.data.contract.starknet_id ? response.data.data.contract.starknet_id.domain : "null";
    return {
      eth_balance: parseFloat(eth_balance).toFixed(4),
      stark_id: stark_id === "null" ? "" : stark_id,
    }
  } catch (e) {
    console.log(e);
    return {
      eth_balance: "",
      stark_id: ""
    }
  }
}

// 获取 Starknet 的tx
export async function getSnTx(addr) {
  try {
    let txData = [];
    let hasNextPage;
    let endCursor;
    const url = "https://starkscan.stellate.sh/";
    const headers = {
      'authority': 'starkscan.stellate.sh',
      'accept': 'application/json',
      'accept-language': 'zh-CN,zh;q=0.9,en;q=0.8,en-GB;q=0.7,en-US;q=0.6',
      'content-type': 'application/json',
    }
    const Json_data = {
      'query': 'query TransactionsTableQuery(\n  $first: Int!\n  $after: String\n  $input: TransactionsInput!\n) {\n  ...TransactionsTablePaginationFragment_transactions_2DAjA4\n}\n\nfragment TransactionsTableExpandedItemFragment_transaction on Transaction {\n  entry_point_selector_name\n  calldata_decoded\n  entry_point_selector\n  calldata\n  initiator_address\n  initiator_identifier\n  main_calls {\n    selector\n    selector_name\n    calldata_decoded\n    selector_identifier\n    calldata\n    contract_address\n    contract_identifier\n    id\n  }\n}\n\nfragment TransactionsTablePaginationFragment_transactions_2DAjA4 on Query {\n  transactions(first: $first, after: $after, input: $input) {\n    edges {\n      node {\n        id\n        ...TransactionsTableRowFragment_transaction\n        __typename\n      }\n      cursor\n    }\n    pageInfo {\n      endCursor\n      hasNextPage\n    }\n  }\n}\n\nfragment TransactionsTableRowFragment_transaction on Transaction {\n  id\n  transaction_hash\n  block_number\n  transaction_status\n  transaction_type\n  timestamp\n  initiator_address\n  initiator_identifier\n  initiator {\n    is_social_verified\n    id\n  }\n  main_calls {\n    selector_identifier\n    id\n  }\n  ...TransactionsTableExpandedItemFragment_transaction\n}\n',
      'variables': {
        'first': 30,
        'after': null,
        'input': {
          'initiator_address': addr,
          'sort_by': 'timestamp',
          'order_by': 'desc',
          'min_block_number': null,
          'max_block_number': null,
          'min_timestamp': null,
          'max_timestamp': null
        }
      }
    }
    const response = await axios.post(url, Json_data, {headers: headers});
    txData = txData.concat(response.data.data['transactions']['edges']);
    // 最新时间
    const timestamp = response.data.data['transactions']['edges'][0]['node']['timestamp'];
    const date = new Date(timestamp * 1000);
    let recentMd = formatDate(date, "M/D");
    let recentAgo = formatDate(date, "AGO");
    hasNextPage = response.data.data['transactions']['pageInfo']['hasNextPage'];
    if (hasNextPage === true) {
      endCursor = response.data.data['transactions']['pageInfo']['endCursor'];
      while (hasNextPage) {
        Json_data['variables']['after'] = endCursor;
        const response = await axios.post(url, Json_data, {headers: headers});
        hasNextPage = response.data.data['transactions']['pageInfo']['hasNextPage'];
        endCursor = response.data.data['transactions']['pageInfo']['endCursor'];
        txData = txData.concat(response.data.data['transactions']['edges']);
      }
    }
    // 整理tx数据，得出 更新/合约数/活跃月/周/日/总
    let [recent, srecent, contracts, months, weeks, days, age] = [recentMd, recentAgo, [], 0, 0, 0, 0];
    let [preYear, preMonth, preDay] = [0, 0, 0];
    for (let i = 0 ; i < txData.length; i++) {
      if (i == txData.length - 1) {
        age = getAge(txData[i]['node']['timestamp'] * 1000);
      }
      // 合约数
      const calls = txData[i]['node']['main_calls'];
      for (let j = 0; j < calls.length; j++) {
        if (contracts.indexOf(calls[j]["contract_address"]) < 0) {
          contracts.push(calls[j]["contract_address"]);
        }
      }
      // 判断月周日活跃
      const nowTxDatetime = txData[i]["node"]["timestamp"];
      const date = new Date(nowTxDatetime * 1000);
      const year = date.getFullYear();
      const month = date.getMonth() + 1; // 获取月份，需要加1
      const day = date.getDate();
      if ((year == preYear) && (month != preMonth) || (year != preYear)) {
        months++;
      }
      // 判断周
      let md1 = toMonday(date);
      let md2 = toMonday(new Date(preYear+ "-" + preMonth + "-" + preDay));
      if (md1 != md2) {
        weeks++;
      }
      // 判断日
      if (!((year == preYear) && (month == preMonth) && (day == preDay))) {
        days++;
      }
      [preYear, preMonth, preDay] = [year, month, day];
    }
    // return tx分析结果，更新/合约数/活跃月/周/日/总/tx
    return [recent, srecent, contracts.length, months, weeks, days, age, txData.length];
  } catch (error) {
    console.error(error);
    return ["", "", "", "", "", "", ""];
  }
}

// 获取 Starknet 的token tx
export async function getSnTokenTx(addr) {
  try {
    let txData = [];
    let hasNextPage;
    let endCursor;
    const url = "https://starkscan.stellate.sh/";
    const headers = {
      'authority': 'starkscan.stellate.sh',
      'accept': 'application/json',
      'accept-language': 'zh-CN,zh;q=0.9,en;q=0.8,en-GB;q=0.7,en-US;q=0.6',
      'content-type': 'application/json',
    };
    const Json_data = {
      'query': 'query ERC20TransferEventsTableQuery(\n  $first: Int!\n  $after: String\n  $input: ERC20TransferEventsInput!\n) {\n  ...ERC20TransferEventsTablePaginationFragment_erc20TransferEvents_2DAjA4\n}\n\nfragment ERC20TransferEventsTablePaginationFragment_erc20TransferEvents_2DAjA4 on Query {\n  erc20TransferEvents(first: $first, after: $after, input: $input) {\n    edges {\n      node {\n        id\n        ...ERC20TransferEventsTableRowFragment_erc20TransferEvent\n        __typename\n      }\n      cursor\n    }\n    pageInfo {\n      endCursor\n      hasNextPage\n    }\n  }\n}\n\nfragment ERC20TransferEventsTableRowFragment_erc20TransferEvent on ERC20TransferEvent {\n  id\n  transaction_hash\n  from_address\n  from_erc20_identifier\n  from_contract {\n    is_social_verified\n    id\n  }\n  transfer_from_address\n  transfer_from_identifier\n  transfer_from_contract {\n    is_social_verified\n    id\n  }\n  transfer_to_address\n  transfer_to_identifier\n  transfer_to_contract {\n    is_social_verified\n    id\n  }\n  transfer_amount\n  transfer_amount_display\n  timestamp\n  main_call {\n    selector_identifier\n    id\n  }\n}\n',
      'variables': {
        'first': 30,
        'after': null,
        'input': {
          'transfer_from_or_to_address': addr,
          'call_invocation_type': 'FUNCTION',
          'sort_by': 'timestamp',
          'order_by': 'desc'
        }
      }
    };
    let response = await axios.post(url, Json_data, {headers: headers});
    txData = txData.concat(response.data.data['erc20TransferEvents']['edges']);
    hasNextPage = response.data.data['erc20TransferEvents']['pageInfo']['hasNextPage'];
    if (hasNextPage) {
      endCursor = response.data.data['erc20TransferEvents']['pageInfo']['endCursor'];
      while (hasNextPage) {
        Json_data.variables.after = endCursor;
        let response2 = await axios.post(url, Json_data, {headers: headers});
        hasNextPage = response2.data.data['erc20TransferEvents']['pageInfo']['hasNextPage'];
        endCursor = response.data.data['erc20TransferEvents']['pageInfo']['endCursor'];
        txData = txData.concat(response.data.data['erc20TransferEvents']['edges']);
      }
    }
    // 整理tx数据，得出 bridge
    let [bridge, erc20e, erc20u] = [0, 0, 0];
    for (let i = 0 ; i < txData.length; i++) {
      // 官桥充值操作
      if (txData[i]["node"]["from_address"] == "0x049d36570d4e46f48e99674bd3fcc84644ddd6b96f7c741b1562b82f9e004dc7" &&
        txData[i]["node"]["main_call"] && txData[i]["node"]["main_call"]["selector_identifier"] == "handle_deposit") {
        bridge++;
        let token = txData[i]["node"]["from_erc20_identifier"];
        if (token == "StarkGate: ETH") {
          erc20e += Number(txData[i]["node"]["transfer_amount"]) / (10**18);
        } else if (token == "StarkGate: USDT" || token == "StarkGate: USDC") {
          erc20u += Number(txData[i]["transfer"]["transfer_amount"]) / (10**6);
        }
      }
    }
    // return token tx分析结果，bridgeTx/bridgeU
    return [bridge, erc20e.toFixed(4) + "/" + erc20u.toFixed(1)];
  } catch (e) {
    console.log(e);
    return ["", ""];
  }
}



