// 时间差分
export function formatDate(date, patter) {
  let date2 = new Date(date);
  let year = date2.getFullYear();
  let month = date2.getMonth();
  let day = date2.getDate();
  month = month + 1;
  // if (month < 10) month = "0" + month;
  // if (day < 10) day = "0" + day;
  if (patter == "Y/M/D") {
    return year + "/" + month + "/" + day;
  } else if (patter == "M/D") {
    return month + "/" + day;
  } else if (patter == "AGO") {
    // 时间差分
    const utc8Date = new Date(date2.getTime());
    const now = new Date();
    const utc8Now = new Date(now.getTime());
    const diff = utc8Now - utc8Date;
    const diffInHours = Math.floor(diff / (1000 * 60 * 60));
    const diffInDays = Math.floor(diffInHours / 24);
    let formatDate;
    // if (diffInDays > 30) {
    //   formatDate = month + "/" + day;
    // } else
    if (diffInDays > 0) {
      formatDate = diffInDays + "天前";
    } else if (diffInHours > 0) {
      formatDate = diffInHours + "小时前";
    } else {
      formatDate = "刚刚";
    }
    return formatDate;
  }
}

// 时间age，多少天
export function getAge(datetime) {
  const date = new Date(datetime);
  const offset = 8;
  const utc8Date = new Date(date.getTime() + offset * 3600 * 1000);
  const now = new Date();
  const utc8Now = new Date(now.getTime() + offset * 3600 * 1000);
  const diff = utc8Now - utc8Date;
  const diffInHours = Math.floor(diff / (1000 * 60 * 60));
  return Math.floor(diffInHours / 24)
}

// 日期转换为周一
export function toMonday(datetime) {
  let dte = new Date(datetime);
  let day = dte.getDay();
  let dty = dte.getDate();
  if (day == 0) {
    day=7;
  }
  dte.setDate(dty - day + 1);
  return dte.getFullYear() + '-' + dte.getMonth()+ '-' + dte.getDate();
}

// 降序排序
export function sortTransfer(a, b) {
  const valueA = parseInt(a.amount, 16) * 10 ** -a.tokenInfo.decimals * a.tokenInfo.usdPrice;
  const valueB = parseInt(b.amount, 16) * 10 ** -b.tokenInfo.decimals * b.tokenInfo.usdPrice;
  return valueB - valueA;
}

// 降序排序
export function sortContract(a, b) {
  const valueA =  a.tx;
  const valueB = b.tx;
  return valueB - valueA;
}



