export function analyzeZkTx(transactions, address) {
  let contractData = [];
  let newContractData = [];
  let bridgeData;
  let newContract = [];
  // 初始化
  bridgeData = {
    tx: 0,
    erc20u: 0,
    recent: "无"
  };
  for (let i = 0; i < CONTRACT_INFO.length - 1; i++) {
    contractData[i] = {
      tx: 0,
      erc20u: 0,
      recent: "无"
    };
  }
  for (let t = 0; t < transactions.length; t++) {
    const transaction = transactions[t];
    // 官桥，数据格式不同
    if (transaction.to.toLowerCase() === address.toLowerCase() && transaction.isL1Originated) {
      bridgeData["tx"] += 1;
      bridgeData["erc20u"] += transaction.ethPrice*Number(transaction.value)/(10**18);
      if (bridgeData["recent"] == "无") {
        bridgeData["recent"] = formatDate(transaction["receivedAt"], "AGO");
      }
    } else {
      let isDapp = false;
      for (let i = 0; i < CONTRACT_INFO.length - 1; i++) {
        if (CONTRACT_INFO[i].address.includes(transaction.to.toLowerCase())) {
          isDapp = true;
          contractData[i]["tx"] += 1;
          contractData[i]["erc20u"] += transaction.ethPrice*Number(transaction.value)/(10**18);
          if (contractData[i]["recent"] == "无") {
            contractData[i]["recent"] = formatDate(transaction["receivedAt"], "AGO");
          }
          break;
        }
      }
      // 合约不在列表中
      if (!isDapp) {
        // 新建合约列表
        if (newContract.indexOf(transaction.to.toLowerCase()) < 0) {
          newContract.push(transaction.to.toLowerCase());
          let len = newContract.length;
          newContractData[len-1] = {
            tx: 0,
            erc20u: 0,
            recent: "无"
          };
        }
        let index = newContract.indexOf(transaction.to.toLowerCase());
        if (index >= 0) {
          const contract = transaction.to.toLowerCase();
          newContractData[index]["contract"] = contract;
          newContractData[index]["scontract"] = contract.substring(0,6) + "..." + contract.substring(contract.length-4, contract.length);
          newContractData[index]["tx"] += 1;
          newContractData[index]["erc20u"] += transaction.ethPrice*Number(transaction.value)/(10**18);
          if (newContractData[index]["recent"] == "无") {
            newContractData[index]["recent"] = formatDate(transaction["receivedAt"], "AGO");
          }
        }
      }
    }
  }
  if (!!bridgeData) {
    contractData.push(bridgeData);
  }
  for (let j = 0; j < CONTRACT_INFO.length; j++) {
    contractData[j]["name"] = CONTRACT_INFO[j]["name"];
    contractData[j]["id"] = CONTRACT_INFO[j]["id"];
    contractData[j]["url"] = CONTRACT_INFO[j]["url"];
  }
  // 排序
  const contractDataRt = contractData.sort(sortContract);
  const newContractDataRt = newContractData.sort(sortContract);
  return [contractDataRt, newContractDataRt];
}



